import React from "react";

import resumeArray from "portfolio-website-shared/src/assets/resume";
import { connect } from "react-redux";
import "../styles/components/ResumeSection.scss";
import { ResumeSectionProps } from "../types/components/ResumeSection";
import { StoreRootState } from "../types/state";
import FadeInSection from "./FadeInSection";
import ResumeCard from "./ResumeCard";

const mapStateToProps = (state: StoreRootState) => {
    const { sectionsFade } = state.settingsReducer;
    return {
        sectionsFade,
    };
};

const ResumeSection: React.FunctionComponent<ResumeSectionProps> = (
    props: ResumeSectionProps,
): JSX.Element => {
    const languageIndex = 0;

    return (
        <>
            <FadeInSection fadeToggle={props.sectionsFade}>
                {resumeArray &&
                    resumeArray.map((resumeSection) => {
                        return (
                            <div
                                className="resume-section"
                                key={`resume-${resumeSection[0]}`}
                                id={`resume-${resumeSection[0]}`}
                            >
                                <h1 className="resume-section__title-1">
                                    {resumeSection[1][languageIndex]}
                                </h1>
                                {resumeSection[2] &&
                                    resumeSection[2].map((resumeSubsection) => {
                                        return (
                                            <div
                                                className="resume-subsection"
                                                key={`resume-${resumeSection[0]}-${resumeSubsection[0]}`}
                                                id={`resume-${resumeSection[0]}-${resumeSubsection[0]}`}
                                            >
                                                {resumeSubsection[1][
                                                    languageIndex
                                                ] !== "" && (
                                                    <h2 className="resume-section__title-2">
                                                        {
                                                            resumeSubsection[1][
                                                                languageIndex
                                                            ]
                                                        }
                                                    </h2>
                                                )}
                                                <section
                                                    className={`main-section--${resumeSubsection[2]}`}
                                                >
                                                    {resumeSubsection[4] &&
                                                        resumeSubsection[4].map(
                                                            (resumeElement) => {
                                                                return (
                                                                    <ResumeCard
                                                                        key={`resume-${resumeSection[0]}-${resumeSubsection[0]}-${resumeElement[0]}`}
                                                                        keyId={`${resumeSection[0]}-${resumeSubsection[0]}-${resumeElement[0]}`}
                                                                        template={
                                                                            resumeSubsection[3]
                                                                        }
                                                                        data={
                                                                            resumeElement
                                                                        }
                                                                    />
                                                                );
                                                            },
                                                        )}
                                                </section>
                                            </div>
                                        );
                                    })}
                            </div>
                        );
                    })}
            </FadeInSection>
        </>
    );
};

export default connect(mapStateToProps)(ResumeSection);
