import React from "react";
import "../styles/components/FadeInSection.scss";
import { FadeInSectionProps } from "../types/components/FadeInSection";

const FadeInSection = (props: FadeInSectionProps): JSX.Element => {
    const divRefs: React.MutableRefObject<(HTMLDivElement | null)[]> =
        React.useRef([]);

    React.useEffect(() => {
        if (props.fadeToggle) {
            const observer = new IntersectionObserver((entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        entry.target.className =
                            "fade-in-section fade-in-section-is-visible";
                    } else {
                        entry.target.className = "fade-in-section";
                    }
                });
            });
            divRefs.current.forEach((currentElement) => {
                if (currentElement !== null) {
                    observer.observe(currentElement);
                }
            });

            return () => {
                observer.disconnect();
            };
        } else {
            divRefs.current.forEach((currentElement) => {
                if (currentElement !== null) {
                    currentElement.className =
                        "fade-in-section fade-in-section-is-visible";
                }
            });
        }
    }, [props.fadeToggle]);

    return (
        <>
            {Array.isArray(props.children) ? (
                props.children.map((child, index) => {
                    return (
                        <div
                            key={child.key}
                            ref={(l) => (divRefs.current[index] = l)}
                        >
                            {child}
                        </div>
                    );
                })
            ) : (
                <div ref={(l) => (divRefs.current[0] = l)}>
                    {props.children}
                </div>
            )}
        </>
    );
};

export default FadeInSection;
