import React, { ChangeEvent, ChangeEventHandler } from "react";
import { connect, useStore } from "react-redux";
import { BackgroundShape } from "../enums/background-shape";
import "../styles/components/SettingModal.scss";
import { SettingModalProps } from "../types/components/SettingModal";
import { StoreRootState } from "../types/state";

const mapStateToProps = (state: StoreRootState) => {
    const {
        showSettingsModal,
        backgroundShape,
        backgroundAutoRotate,
        sectionsFade,
    } = state.settingsReducer;

    return {
        show: showSettingsModal,
        backgroundShape,
        backgroundAutoRotate: backgroundAutoRotate,
        sectionsFade: sectionsFade,
    };
};

const SettingModal: React.FunctionComponent<SettingModalProps> = (
    props: SettingModalProps,
): JSX.Element => {
    const store = useStore();

    const onBackgroundShapeChange =
        (shape: BackgroundShape): (() => void) =>
        (): void => {
            store.dispatch({ type: "setBackgroundShape", payload: shape });
        };

    const backgroundShapeLabels = {
        sphere: "Sphere",
        helix: "Helix",
        grid: "Grid",
    };

    const onCloseClick = (): void => {
        store.dispatch({ type: "setShowSettingsModal", payload: "false" });
    };

    const onBackgroundAutoRotateChange = (
        event: ChangeEvent<HTMLInputElement>,
    ): void => {
        store.dispatch({
            type: "setBackgroundAutoRotate",
            payload: event.target.checked ? "true" : "false",
        });
    };

    const onSectionsFadeChange = (
        event: ChangeEvent<HTMLInputElement>,
    ): void => {
        store.dispatch({
            type: "setSectionsFade",
            payload: event.target.checked ? "true" : "false",
        });
    };

    const renderCheckbox = (
        label: string,
        onChange: ChangeEventHandler<HTMLInputElement> | undefined,
        defaultChecked: boolean,
    ): JSX.Element => {
        return (
            <>
                <p className="setting-panel__label">{label}</p>
                <input
                    type="checkbox"
                    className="setting-panel__checkbox"
                    onChange={onChange}
                    defaultChecked={defaultChecked}
                ></input>
            </>
        );
    };

    return (
        <>
            {props.show && (
                <div
                    className="setting-panel__background"
                    role="button"
                >
                    <div className="setting-panel">
                        <h1 className="setting-panel__title">Settings</h1>
                        {renderCheckbox(
                            "Sections Fade In",
                            onSectionsFadeChange,
                            props.sectionsFade,
                        )}
                        {renderCheckbox(
                            "Background Auto Rotate",
                            onBackgroundAutoRotateChange,
                            props.backgroundAutoRotate,
                        )}
                        <h2 className="setting-panel__subtitle">
                            Background Card Layout
                        </h2>
                        {[
                            BackgroundShape.SPHERE,
                            BackgroundShape.HELIX,
                            BackgroundShape.GRID,
                        ].map(
                            (backgroundShape: BackgroundShape): JSX.Element => {
                                return (
                                    <React.Fragment key={backgroundShape}>
                                        <p className="setting-panel__label">
                                            {
                                                backgroundShapeLabels[
                                                    backgroundShape
                                                ]
                                            }
                                        </p>
                                        <input
                                            type="radio"
                                            className="setting-panel__checkbox"
                                            name="background-shape"
                                            onChange={onBackgroundShapeChange(
                                                backgroundShape,
                                            )}
                                            defaultChecked={
                                                props.backgroundShape ===
                                                backgroundShape
                                            }
                                        ></input>
                                    </React.Fragment>
                                );
                            },
                        )}
                        <input
                            onClick={onCloseClick}
                            type="button"
                            value="Close"
                            className="setting-panel__button"
                        />
                    </div>
                </div>
            )}
        </>
    );
};

export default connect(mapStateToProps)(SettingModal);
